<template>
    <div class="users-dump">
        <SideBar />
        <div class="iframe">
            <HeaderManagement />
            <BodyManagement />

            <div class="title-section">
                <h2 class="heading-section">Subscribers</h2>
                <span>
                    Email address <b>dump</b> list of all my subscribers.
                </span>
            </div>

            <div class="d-table">
                <span 
                    v-for="user in users"
                    :key="user.id"
                >
                    {{ user.email }};
                </span>
            </div>
        </div>
    </div>
</template>

<script>
import SideBar from '../components/SideBar.vue'
import HeaderManagement from '../components/HeaderManagement.vue'
import BodyManagement from '../components/BodyManagement.vue'

import usersColRef from '@/firebase'
import { getDocs } from '@firebase/firestore'

export default {
    name: 'UsersDump',
    components: {
        SideBar,
        HeaderManagement,
        BodyManagement
    },
    data() {
        return {
            users: [],
            selectedDoc: null
        }
    },
    methods: {
        async getList () {
            const usersSnapShot = await getDocs(usersColRef)
            const users = []
            usersSnapShot.forEach((user) => {
                const userData = user.data()
                userData.id = user.id
                users.push(userData)
            })
            this.users = users
        }
    },
    created () {
        this.getList()
    }
}
</script>

